/* eslint-disable */
<template>
	<div class="event">
		<b-container>
			<div class="event_header d-flex align-items-center justify-content-between mt-4">
				<div class="h4">{{ eventName }}</div>
				<div class="d-flex">
					<div class="btn_icon mr-2" v-b-tooltip.bottom.hover title="Reschedule" v-if="isOneOnOne">
						<b-icon icon="calendar2-event" font-scale="1.4"></b-icon>
					</div>
					<div class="btn_icon mr-2" v-b-tooltip.bottom.hover title="Cancel">
						<b-icon icon="x-circle" font-scale="1.4"></b-icon>
					</div>
				</div>
			</div>
			<div class="event_body mt-5">
				<div class="field_title">
					<div class="title">
						<b-icon icon="link" scale="1.2" class="mr-2"></b-icon>
						<span class="title_text">LINK</span>
					</div>
					<div class="body mt-2">
						<router-link :to="{ name: 'home' }" class="text-decoration-none">{{ event.invite && event.invite.name }}</router-link>
					</div>
				</div>
				<div class="field_title mt-4">
					<div class="title">
						<b-icon icon="calendar2-event" scale="0.9" class="mr-2"></b-icon>
						<span class="title_text">DATE</span>
					</div>
					<div class="body mt-2">
						<span class="text-success font-weight-600">{{ startDate }}</span>
					</div>
				</div>
				<div class="field_title mt-4" v-if="locationName">
					<div class="title">
						<b-icon icon="geo-alt" scale="0.9" class="mr-2"></b-icon>
						<span class="title_text">LOCATION</span>
					</div>
					<div class="body mt-2 fs-md">
						<span>{{ locationName | toCapitalize }}</span>
						<template v-if="onlineMeeting">
							<span> - </span>
							<a :href="onlineMeeting.url" class="text-decoration-none">Join Meeting</a>
						</template>
					</div>
				</div>
				<template v-if="!isOneOnOne">
					<div class="field_title mt-4">
						<div class="title">
							<b-icon icon="grid3x2" scale="0.9" class="mr-2"></b-icon>
							<span class="title_text">ATTENDANCE</span>
						</div>
						<div class="body mt-2">
							<span class="">{{ event.invitees.length }} of {{ event.invite.maxInvitees }} spots filled</span>
						</div>
					</div>
					<div class="field_title mt-4">
						<div class="title">
							<b-icon icon="people" scale="0.9" class="mr-2"></b-icon>
							<span class="title_text">INVITEES</span>
						</div>
						<div class="body mt-2">
							<div v-for="(invitee, i) in event.invitees" :key="i" @click="showInviteeData(invitee)">
								<span class="text-primary cursor-pointer"> {{ invitee.name }} </span>
							</div>
						</div>
					</div>
				</template>
				<template v-else>
					<div class="field_title mt-4">
						<div class="title">
							<b-icon icon="person" scale="0.9" class="mr-2"></b-icon>
							<span class="title_text">INVITEE</span>
						</div>
						<div class="body mt-2">
							<div>{{ invitee.name }}</div>
							<div class="text-muted mt-1">{{ invitee.email }}</div>
						</div>
					</div>
					<div class="field_title mt-4" v-if="guests.length">
						<div class="title">
							<b-icon icon="people" scale="0.9" class="mr-2"></b-icon>
							<span class="title_text">GUESTS</span>
						</div>
						<div class="body mt-2">
							<div v-for="(guest, i) in guests" :key="i">
								<div class="text-muted">{{ guest.email }}</div>
							</div>
						</div>
					</div>
				</template>
			</div>
		</b-container>
		<invitee-modal :invitee.sync="inviteeData"></invitee-modal>
	</div>
</template>
<script>
	import InviteeModal from "@/components/modals/InviteeModal.vue";
	export default {
		components: { InviteeModal },

		data() {
			return {
				inviteeData: null,
			};
		},

		mounted() {
			this.$store.dispatch("getEvent", this.$route.params.id);
			this.$bvModal.show("InviteeModal");
		},

		watch: {
			inviteeData(invitee) {
				if (!invitee) {
					this.$bvModal.hide("InviteeModal");
				}
			},
		},

		computed: {
			event() {
				return this.$store.state.events.one;
			},

			isOneOnOne() {
				return this.event.invite?.type == "oneOnOne";
			},

			locationName() {
				if (!this.event.invite?.location?.type) return "";

				if (this.invite.location.name == "custom") {
					return this.invite.location.text;
				}

				return this.event.invite?.location?.name;
			},

			onlineMeeting() {
				return this.event.onlineMeeting;
			},

			invitee() {
				return this.event?.invitees?.find((invitee) => !invitee.isGuest);
			},

			guests() {
				return this.event?.invitees?.filter((invitee) => invitee.isGuest);
			},

			eventName() {
				let eventName = this.event.invite?.name;

				if (!eventName) return "";

				if (!this.isOneOnOne) return eventName;

				let organizer = this.event.invite.organizer;

				let invitee = this.event.invitees.find((invitee) => !invitee.isGuest);

				organizer = organizer.firstName + " " + organizer.lastName;

				return invitee.name + " and " + organizer;
			},

			startDate() {
				let startDate = new Date(this.event.startDate);

				let date = startDate.toLocaleDateString("en-US", {
					weekday: "short",
					month: "short",
					day: "2-digit",
					hour: "numeric",
					minute: "numeric",
				});

				let endTime = new Date(startDate.getTime() + this.event.duration * 60 * 1000);

				endTime = endTime.toLocaleTimeString("en-US", { hour: "numeric", minute: "numeric" });

				return date + " - " + endTime;
			},
		},

		methods: {
			showInviteeData(invitee) {
				this.inviteeData = invitee;

				this.$nextTick(() => this.$bvModal.show("InviteeModal"));
			},
		},
	};
</script>
